'use strict';

import { isIE } from './ieError';
import { clickElement } from './clickElement';

isIE();


/*
 * btnにclickのする要素のid名
 * getIdに追加するクラス名
*/

const content: contentMap = {
  resume: {
    btn: 'js-btnResume',
    getId: 'resume'
  },
  work: {
    btn: 'js-btnWork',
    getId: 'work'
  },
  blog: {
    btn: 'js-btnBlog',
    getId: 'blog'
  }
};

interface contentMap {
  [key: string]: {
    [btn: string]: string;
  }
};

{
  Object.keys(content).forEach((key) => {
    new clickElement(content[key]['btn'], content[key]['getId'], 'is-show').showContent();
  });
  new clickElement('js-topBtn', 'js-topMenu', 'is-none').showTop();
  new clickElement('js-topBtn', 'js-content', 'is-show').showTop();
  new clickElement('js-topBtn', 'js-menu', 'is-none').showMenu();
  new clickElement('js-topBtn', 'js-topName', 'is-rotation').rotationTopName();
}

{
  const btnClose = document.getElementById('js-btnCloseContent');
  const topMenu = document.getElementById('js-topMenu');
  const topNames = Array.from(document.getElementsByClassName('js-topName'));
  const menu = document.getElementById('js-menu');
  const contentBlock = document.getElementById('js-content');
  if (btnClose !== null) {
    btnClose.onclick = () => {
      if (topMenu !== null) {
        topMenu.classList.remove('is-none');
      }

      for (const topName of topNames) {
        const elem = <HTMLElement>topName;
        elem.classList.remove('is-rotation')
      }

      if (menu !== null) {
        menu.classList.add('is-none');
      }

      if (contentBlock !== null) {
        contentBlock.classList.remove('is-show');
      }
    Object.keys(content).forEach((key) => {
      let elemRemove = document.getElementById(content[key]['content']);
      if (elemRemove !== null) {
        elemRemove.classList.remove('is-show')
      }
      });
    }
  }
}
