export class clickElement {
  btn: string;
  getElement: string;
  addClass: string;

  constructor(btn: string, getElement: string, addClass: string) {
    this.btn = btn;
    this.getElement = getElement;
    this.addClass = addClass
  }
  showContent() {
    let btnContents = Array.from(document.getElementsByClassName(this.btn));
    let elemGetId = document.getElementById(this.getElement);
    let elemAddClasses = document.getElementsByClassName('js-container');

    for (const btnContent of btnContents) {
      const elemContent = <HTMLElement> btnContent;
      elemContent.addEventListener('click', () => {
        Object.values(elemAddClasses).forEach((elem) => {
          elem.classList.add('is-none');
          elem.classList.remove(this.addClass);
        });
        if (elemGetId !== null) {
          elemGetId.classList.remove('is-none');
          elemGetId.classList.add(this.addClass);
        }
      },false)
    }
  }

  showTop() {
    let btnContents = Array.from(document.getElementsByClassName(this.btn));
    let elemGetId = document.getElementById(this.getElement);
    for (const btnContent of btnContents) {
      const elemContent = <HTMLElement>btnContent;
      elemContent.addEventListener('click', () => {
        if (elemGetId !== null) {
          elemGetId.classList.add(this.addClass);
        }
      }, false);
    }
  }

  showMenu() {
    let btnContents = Array.from(document.getElementsByClassName(this.btn));
    let elemGetId = document.getElementById(this.getElement);
    for (const btnContent of btnContents) {
      const elemContent = <HTMLElement>btnContent;
      elemContent.addEventListener('click', () => {
        if (elemGetId !== null) {
          elemGetId.classList.remove(this.addClass);
        }
      }, false);
    }
  }

  rotationTopName() {
    let btnContents = Array.from(document.getElementsByClassName(this.btn));
    let rotations = Array.from(document.getElementsByClassName(this.getElement));
    for (const btnContent of btnContents) {
      const elemContent = <HTMLElement>btnContent;
      elemContent.addEventListener('click', () => {
        for (const rotation of rotations) {
          const elem = <HTMLElement>rotation;
          elem.classList.add(this.addClass);
        }
      }, false);
    }
  }
}
